import React from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        //background: '#00FF0088', 
    },
    ContentsText: {
        textAlign: 'left'
    }
}));

export const PortraitMasterContentsIntro = (props) => {
    console.log('PortraitMasterContentsIntro component start');
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <center><h2><b>스마트폰 인물사진 마스터</b></h2>
                <img src={process.env.PUBLIC_URL + '/img/camera-master-intro.jpg'} />;
                <h5>오드리 헵번 by 유수프 카쉬, 1956</h5>
            </center>
            <div className={classes.ContentsText}>
                <h4>
                    스마트폰으로 <b>인물사진 마스터</b>에 도전합니다.
                </h4>
                <h5>
                    늘 우리와 함께 하는 스마트폰. 작고 가벼운 스마트폰이지만, 스마트폰 카메라의 성능은 결고 가볍지 않습니다. 무려 <b>촬영, 편집, 보정을 한 큐에 끝낼 수 있는</b> 강력한 도구죠.
<br /><br />
모델의 마음까지 훔치는 스마트폰 인물사진 노우-하우를 <b>마스터와 함께</b> 배워 나갑니다. 퀘스트 특성에 맞는 맞춤 카메라를 제공해 드리니, 한 발짝씩 쉽게 따라오실 수 있을 거예요.
</h5>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterContentsIntro);