import React, { useEffect, useState }  from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';
import { DeleteCanvasData } from '../actions/camera'
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
    },
    bottomBar: {
        position: "fixed",
        height: '50px',
        bottom: "0px",
        width: "100%",
        backgroundColor: '#000000AA',
    },
    upload_button: {
        position: "absolute",
        top: "5px",
        left: "20px",
        height: "30px",
    },
    delete_button: {
        position: "absolute",
        top: "5px",
        right: "20px",
        height: "30px",
    },
}));

export const PortraitMasterCaptureImageControlBar = ({set_scene, itemCount, onClickUploadImage, onClickDeleteImage, DeleteCanvasData}) => {
    console.log('PortraitMasterCaptureImageControlBar component start');
    const classes = useStyles();

    let uploadImageButton;
    let deleteImageButton;

    if (itemCount != 0) {
        uploadImageButton = <img className={classes.upload_button} onClick={onClickUploadImage} src={process.env.PUBLIC_URL + '/img/image-select-upload-enable-icon.png'} />;
        deleteImageButton = <img className={classes.delete_button} onClick={onClickDeleteImage} src={process.env.PUBLIC_URL + '/img/image-select-delete-enable-icon.png'} />;
    } else {
        uploadImageButton = <img className={classes.upload_button} src={process.env.PUBLIC_URL + '/img/image-select-upload-disable-icon.png'} />;
        deleteImageButton = <img className={classes.delete_button} src={process.env.PUBLIC_URL + '/img/image-select-delete-disable-icon.png'} />;
    }
    
    return (
        <div className={classes.root}> 
            <div className={classes.bottomBar} >
                {uploadImageButton}
                {deleteImageButton}
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene, DeleteCanvasData };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterCaptureImageControlBar);