import React from 'react';

import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';

import PortraitMaster from './../components/PortraitMaster'
import CameraCapture from './../components/camera/CameraCapture'
import CameraCaptureSelect from './../components/camera/CameraCaptureSelect'
import PortraitMasterGridCaptureImageList from '../components/PortraitMasterGridCaptureImageList'
import PortraitMasterFullScreenCaptureList from './../components/PortraitMasterFullScreenCaptureList'
import SceneNavigation from './SceneNavigation'

const useStyles = makeStyles((theme) => ({
  root: {
      width: "100%",
      height: "100%"
  },
}));


export const IndexScene = ({ _current_scene, set_scene }) => {
  console.log('Index_scene component start');
  const classes = useStyles();

  const getActiveScene = () => {
    switch(_current_scene) {
      case 'CameraCapture': {
        return (<CameraCapture />);
      }
      case 'PortraitMaster': {
        return (<PortraitMaster />);
      }
      case 'CameraCaptureSelect': {
        return (<CameraCaptureSelect />);
      }
      case 'GridCaptureList': {
        return (<PortraitMasterGridCaptureImageList />);
      }
      case 'FullScreenCaptureList': {
        return (<PortraitMasterFullScreenCaptureList />);
      }
      default: {
        return (<PortraitMaster />); // SceneNavigation
      }
    }
  }

  return (
    <div className={classes.root}>
      {getActiveScene()}
    </div>
  );
}

const mapStateToProps = (state) => ({
  _current_scene: state.scene._current_scene
});
const mapDispatchToProps = { set_scene };

export default connect(mapStateToProps, mapDispatchToProps)(IndexScene);