import React, { useEffect, useState }  from 'react';
import { connect } from 'react-redux';
import { set_scene, SetSelectImgIndex } from '../actions/scene';
import { DeleteCanvasData } from '../actions/camera'
import { makeStyles } from '@material-ui/core/styles';

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';
import { PortraitMasterCaptureImageControlBar } from './PortraitMasterCaptureImageControlBar';

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        background: '#000000', 
    },
    multi_select_text: {
        color: 'white'
    },
    ImgList: {
        overflowY: "auto",
        background: '#000000', 
    },
}));

var _items = [];
export const PortraitMasterFullScreenCaptureList = ({_canvasData, set_scene, SetSelectImgIndex, _selectImgIndex, DeleteCanvasData}) => {
    console.log('PortraitMasterFullScreenCaptureList component start');
    const classes = useStyles();
    const [itemCount, setItemCount] = useState(0);

    useEffect(() => {
        onUpdateImageList();
      });

    const onUpdateImageList = () => {
        _items = [];
        console.log('onUpdateImageList');
        if (undefined != window.HybridApp) {
            var imgCount = window.HybridApp.GetDirectoryFileCount();
            for (var i = 0; i < imgCount; i++) {
                var imgText = window.HybridApp.GetImageDataFromDirectory(i);
                AddImage(i, imgText);
            }
        }
        else {
            for (var i = 0; i < _canvasData.length; i++) {
                AddImage(i, _canvasData[i]);
            }
        }
    }

    const AddImage = (index, imgsrc) => {
        _items.push(imgsrc);
        setItemCount(_items.length);
        console.log('item count:' + _items.length);
    }

    const onClickUploadImage = () => {
        console.log("onClickUploadImage call");
        set_scene('PortraitMaster');
    }

    const onClickDeleteImage = () => {
        console.log("onClickDeleteImage call index:" + _selectImgIndex);
        if (undefined != window.HybridApp) {
            window.HybridApp.DeleteImageDataFromDirectory(_selectImgIndex);
        }
        else {
            DeleteCanvasData(_selectImgIndex);
        }

        SetSelectImgIndex(0);
        if (_items.length <= 1) {
            set_scene('PortraitMaster');
        }
    }

    const onSelectImage = (idx) => {
        console.log("onSelectImage call:" + idx);
        SetSelectImgIndex(idx);
    }

    const getCarousel = () => {
        console.log('_items count:' + _items.length);
        return <Carousel infiniteLoop={true} selectedItem={_selectImgIndex} showArrows={true} showStatus={true} showThumbs={false} onChange={onSelectImage}>
      {
        _items.map( image => {
          return <img src={image} />
        })
      }
    </Carousel>
    }

    let selectText;
    if (itemCount > 1) {
        selectText = <div className={classes.multi_select_text}><center>슬라이드하면 다른 사진 표시</center></div>;
    } else {
        selectText = <div className={classes.multi_select_text}></div>;
    }

    console.log('itemCount renderring update');
    return (
        <div className={classes.root}>
            {selectText}
            {getCarousel()}
            <PortraitMasterCaptureImageControlBar itemCount={1} onClickUploadImage={onClickUploadImage} onClickDeleteImage={onClickDeleteImage} />
        </div >
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene,
    _canvasData: state.camera._canvasData,
    _selectImgIndex: state.scene._selectImgIndex
});
const mapDispatchToProps = { set_scene, SetSelectImgIndex, DeleteCanvasData };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterFullScreenCaptureList);