import React, { useEffect, useState }  from 'react';
import { connect } from 'react-redux';
import { set_scene } from '../actions/scene';
import { DeleteCanvasData } from '../actions/camera'
import { makeStyles } from '@material-ui/core/styles';

import PortraitMasterCaptureImageControlBar from './PortraitMasterCaptureImageControlBar'

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%",
        background: '#000000', 
    },
    select_text: {
        color: 'white'
    },
    ImgList: {
        overflowY: "auto",
        background: '#000000', 
    },
    bottomBar: {
        position: "fixed",
        height: '50px',
        bottom: "0px",
        width: "100%",
        backgroundColor: '#000000AA',
    },
    upload_button: {
        position: "absolute",
        top: "5px",
        left: "20px",
        height: "30px",
    },
    delete_button: {
        position: "absolute",
        top: "5px",
        right: "20px",
        height: "30px",
    },
}));

var _items = [];
export const PortraitMasterGridCaptureImageList = ({_canvasData, set_scene, DeleteCanvasData}) => {
    console.log('PortraitMasterGridCaptureImageList component start');
    const classes = useStyles();
    const [itemCount, setItemCount] = useState(0);

    useEffect(() => {
        onUpdateImageList();
      }, []);

    const onUpdateImageList = () => {
        console.log('onUpdateImageList');
        if (undefined != window.HybridApp) {
            var imgCount = window.HybridApp.GetDirectoryFileCount();
            for (var i = 0; i < imgCount; i++) {
                var imgText = window.HybridApp.GetImageDataFromDirectory(i);
                AddImage(i, imgText);
            }
        }
        else {
            for (var i = 0; i < _canvasData.length; i++) {
                AddImage(i, _canvasData[i]);
            }
        }
    }

    const selectImage = (id) => {
        console.log("selectImage:" + id);
        var deleteState = false;
        for (var i = 0; i < _items.length; i++) {
            if (_items[i] == id) {
                _items.splice(i, 1); 
                deleteState = true;
                break;
            }
        }
        if (false == deleteState) {
            _items.push(id);
        }
        setItemCount(_items.length);
        return !deleteState;
    }

    const getSelectState = (id) => {
        for (var i = 0; i < _items.length; i++) {
            if (_items[i] == id) {
                return true;
            }
        }
        return false;
    }

    function onToggleImage(){
        console.log("onToggleImage:" + this.id);
        var selectState = selectImage(this.id);
        //console.log("onToggleImage:");
        var imgElement = document.getElementById(this.id);
        if (undefined != imgElement) {
            if (true == selectState) {
                imgElement.style.filter = "brightness(150%) grayscale(80%)";
            }
            else {
                imgElement.style.filter = "";
            }
        }

        var checkElement = document.getElementById(this.id + 'Check');
        if (undefined != checkElement) {
            if (true == selectState) {
                //console.log('checkElement block');
                checkElement.style.display = "block";
            }
            else {
                //console.log('checkElement none');
                checkElement.style.display = "none";
            }
        }
    }

    const AddImage = (index, imgsrc) => {
        var imgList = document.getElementById('imagelist');
        if (undefined == imgList) {
            return;
        }

        var divElement = document.createElement('div');
        divElement.id = 'img' + index + 'Root';
        divElement.style.position = "relative";
        imgList.appendChild(divElement);

        var imgElement = document.createElement('img');
        imgElement.id = 'img' + index;
        imgElement.style.position = "relative";
        imgElement.style.width = "100%";
        imgElement.style.height = "auto";
        imgElement.onclick = onToggleImage;
        imgElement.style.margin = "5px";
        imgElement.src = imgsrc;
        divElement.appendChild(imgElement);

        var overlayElement = document.createElement('img');
        overlayElement.id = 'img' + index + 'Check';
        overlayElement.style.position = "absolute";
        overlayElement.style.display = "none";
        overlayElement.style.width = "40px";
        overlayElement.style.height = "auto";
        overlayElement.style.right = "10px";
        overlayElement.style.bottom = "10px";
        overlayElement.src = process.env.PUBLIC_URL + '/img/image-checked-icon.png';
        divElement.appendChild(overlayElement);
    }

    const onClickUploadImage = () => {
        console.log("onClickUploadImage call");
        _items = [];
        set_scene('PortraitMaster');
    }

    const getIndexFromId = (id) => {
        var removeText = 'img';
        var indexText = id.substring(removeText.length);
        try {
            return parseInt(indexText);
        } catch (error) {
        }
        return -1;
    }

    const onClickDeleteImage = () => {
        console.log("onClickDeleteImage call");
        var deleteIndexList = [];
        for (var i = 0; i < _items.length; i++) {
            var img_element = document.getElementById(_items[i] + 'Root');
            if (undefined == img_element) {
                console.log('select item delete error id:' + _items[i]);
                break;
            }
            img_element.remove();
            var selectState = getSelectState(_items[i]);
            if (selectState == true) {
                var index = getIndexFromId(_items[i]);
                deleteIndexList.push(index);
            }
        }
        _items = [];
        setItemCount(0);

        deleteIndexList.sort(function(a, b) {
            return b - a;
        });
        
        for (var i = 0; i < deleteIndexList.length; i++) {
            console.log('select item DeleteCanvasData call index:' + deleteIndexList[i]);
            if (undefined != window.HybridApp) {
                window.HybridApp.DeleteImageDataFromDirectory(deleteIndexList[i]);
            }
            else {
                DeleteCanvasData(deleteIndexList[i]);
            }
        }
        var imgList = document.getElementById('imagelist');
        if (undefined == imgList) {
            return;
        }
        if (imgList.childElementCount == 0) {
            set_scene('PortraitMaster');
        }
    }

    let selectText;

    if (itemCount != 0) {
        selectText = <div className={classes.select_text}><center>{itemCount}개 선택됨</center></div>;
    } else {
        selectText = <div className={classes.select_text}><center>선택 되지 않음</center></div>;
    }
    
    return (
        <div className={classes.root}>
            {selectText}
            <div id="imagelist" className={classes.ImgList} />
            <PortraitMasterCaptureImageControlBar itemCount={itemCount} onClickUploadImage={onClickUploadImage} onClickDeleteImage={onClickDeleteImage} />
        </div >
    );
}

const mapStateToProps = (state) => ({
    _current_scene: state.scene._current_scene,
    _canvasData: state.camera._canvasData
});
const mapDispatchToProps = { set_scene, DeleteCanvasData };

export default connect(mapStateToProps, mapDispatchToProps)(PortraitMasterGridCaptureImageList);